<template>
  <div class="wrapper">
    <div class="label">本年度全系统纯案数</div>
    <typeChange @change="changeType"></typeChange>
    <div class="content">
      <div class="c-top">
        <div class="ct-left">
          <div class="ct-l-l">
            <ctitle :title="'纯案占比'"></ctitle>
            <div class="detail">
              <allCount class="all-count" :count="allcount"></allCount>

              <div class="zbPie" v-loading="!cazbPieParams.data.length"
                   :element-loading-background="$STANDARD.loadingBgColor">
                <div class="pie-label">
                  <p>纯案数</p>
                  <p>信访件总数</p>
                </div>
                <div class="chart">
                  <div class="pie-percent" v-if="cazbPieParams.data[0]">
                    {{(cazbPieParams.data[0].value*100).toFixed(2)}}%
                  </div>
                  <percentPie :params='cazbPieParams'></percentPie>
                </div>
              </div>
            </div>
          </div>
          <div class="ct-l-r">
            <ctitle :title="'四级登记机构占比'"></ctitle>
            <div class="charts">
              <loopPie :params="djjgPieParams"></loopPie>
            </div>
          </div>
        </div>
        <div class="ct-right">
          <div class="echartPie">
            <ctitle :title="'地区分布'"></ctitle>
            <div class="charts">
              <loopPie :params="dqfbPieParams"></loopPie>
            </div>
          </div>
<!--          <div class="echartBar">-->
<!--            <ctitle :title="'信访次数统计'"></ctitle>-->
<!--            <div class="charts">-->
<!--              <barline :params="xfcsBarParams"></barline>-->
<!--            </div>-->
<!--          </div>-->
        </div>
      </div>
      <div class="c-bottom">
        <div class="menu">
          <div class="curitem" @click="menuShow=true">{{menuName}}</div>
          <ul class="menu-list clearfloat" v-if="menuShow">
            <li
              class="item"
              :class="menuIndex===index?'active':''"
              v-for="(item,index) of menuArr"
              :key="index"
              @click="menuChange(index)"
            >
              {{item}}
            </li>
          </ul>
        </div>
        <div class="w-charts">
          <ctitle :title="'地区人群年龄统计'"></ctitle>
          <div class="charts">
            <barline :params="rqnlBarParams"></barline>
          </div>
        </div>
        <div class="w-charts">
          <ctitle :title="'地区信访目的占比'"></ctitle>
          <div class="charts">
            <pie-chart3D :params="xfmdPieParams"></pie-chart3D>
          </div>
        </div>
        <div class="w-charts">
          <ctitle :title="'地区投诉问题占比'"></ctitle>
          <div class="charts">
            <loopPie :params="tswtPieParams"></loopPie>
          </div>
        </div>
<!--        <div class="w-charts">-->
<!--          <ctitle :title="'地区信访次数统计'"></ctitle>-->
<!--          <div class="charts">-->
<!--            <barline :params="dqxfcsBarParams"></barline>-->
<!--          </div>-->
<!--        </div>-->
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import typeChange from "@/components/typeChange/index";
  import incNumber from "@/components/incNumber/index";
  import PieChart3D from "@/components/pie/PieChart3D";
  import loopPie from "@/components/pie/LoopPie";
  import barline from "@/components/bar/BarLine";
  import percentPie from "@/components/pie/percentPie";
  import allCount from "@/components/list/allCount";

  export default {
    name: "cjcj",
    components: {
      ctitle,
      incNumber,
      typeChange,
      PieChart3D,
      loopPie,
      barline,
      allCount,
      percentPie
    },
    data() {
      return {
        loadFlag: false,
        curTypeId:'drop',
        // 各市切换列表
        menuShow: false,
        menuIndex: 0,
        menuName: "石家庄市",
        menuArr: ["石家庄市", "张家口市", "保定市", "秦皇岛市", "廊坊市", "衡水市", "邯郸市", "承德市", "邢台市", "唐山市", "沧州市", "定州市", "辛集市", "雄安新区"],

        // 纯案占比
        allcount: -1,
        cazbPieParams: {
          data: [],
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options: {
            bgColor: {
              start: 'rgba(255,255,255,0.7)',
              end: 'rgba(255,255,255,0.1)'
            },
            GradientColor: {
              start: '#5cd8ff',
              end: '#5cd8ff'
            }
          },
          realRadius: ['55%', '65%'],
          placeHolderRadius: ['55%', '65%'],
        },
        // 四级登记机构占比
        djjgPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.gradientColors,
            radius: ["60%", "75%"],
            center: ["50%", "50%"],
            centerRadius: ["52%", "53%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.22),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 45,
            borderWidth: this.fontSize(0.05),
            bRichPadding: [30, -45, 0, -45],
            tooltip: {
              show: true
            },
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "14%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true
          }
        },
        // 地区分布
        dqfbPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.pieMoreColors,
            radius: ["60%", "75%"],
            center: ["50%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.22),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 50,
            tooltip: {
              show: true
            },
            bRichPadding: [30, -50, 0, -50],
            borderWidth: 5,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "12%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true
          }
        },
        // 信访次数统计
        xfcsBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            xName: "name", //x轴字段
            axisValueShow: true,
            splitLineShow: false, //value轴splitline是否显示
            axisLabelSize: "12%", // label字号
            left: "10%",
            right: "15%",
            top: "5%",
            bottom: "10%",
            barWidth: 30,
            barLabelOptions: "top",
            barLabelShow: true, //柱状图数量值显示
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#ffe97d", "#ff957c"]]
            },
            map: {
              value: "次数"
            }
          }
        },
        // 年龄统计
        rqnlBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            xName: "name", //x轴字段
            axisValueShow: true,
            axis1Name:"人次",
            axisLabelSize: "12%", // label字号
            left: "10%",
            right: "0%",
            top: "5%",
            bottom: "10%",
            barLabelOptions: "top",
            barWidth: 30,
            splitLineShow: false,
            barLabelShow: true, //柱状图数量值显示
            axisLabelColor: "rgba(0, 176, 255, .8)",
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#ffa8c5", "#5850ff"]]
            },
            map: {
              value: "人数"
            }
          }
        },
        // 信访目的
        xfmdPieParams: {
          data: [],
          angle: 20,
          radius: "35%",
          labelRadius: 25,
          fontSize: this.fontSize(0.16),
          color: this.$STANDARD.pieColors,
          styleObj: {
            width: "100%",
            height: "100%"
          }
        },
        // 投诉问题
        tswtPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: this.$STANDARD.pieColors,
            radius: ["45%", "55%"],
            center: ["50%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.22),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 50,
            bRichPadding: [30, -50, 0, -50],
            borderWidth: 0,
            borderColor: "#0B2244",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "12%" //文字大小
            },
            labelSize: "22%", // label字号
            threeLevel:true,
            rollFlag: true
          }
        },
        // 地区信访次数
        dqxfcsBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            xName: "name", //x轴字段
            axisValueShow: true,
            splitLineShow: false, //value轴splitline是否显示
            axisLabelSize: "12%", // label字号
            left: "16%",
            right: "5%",
            top: "5%",
            bottom: "10%",
            barWidth: 30,
            barLabelOptions: "top",
            barLabelShow: true, //柱状图数量值显示
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#d2f69d", "#79cc7a"]]
            },
            map: {
              value: "次数"
            }
          }
        },
      };
    },
    mounted() {
      this.getAPI(this.year, this.curTypeId);
    },
    methods: {
      // 头部类型切换
      changeType(id) {
        this.curTypeId = id;
        this.getAPI(this.year, this.curTypeId);
      },
      // 各市列表切换
      menuChange(index) {
        this.menuIndex = index;
        this.menuName = this.menuArr[this.menuIndex];
        this.menuShow = false;
        this.getBtmAPI(this.year, this.curTypeId, this.menuName);
      },
      getAPI(year, id) {
        // 1.纯案占比（总数）
        get_http("cjcjcaChunaZbTotal", {year: year, xfxs: id})
          .then(res => {
            this.allcount = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 2.纯案占比（饼图）
        get_http("cjcjcaChunaZbPie", {year: year, xfxs: id})
          .then(res => {
            this.cazbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.四级登记机构占比
        get_http("cjcjcaChunaDjjgPie", {year: year, xfxs: id})
          .then(res => {
            this.djjgPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4.地区分布
        get_http("cjcjcaChunaDqfbPie", {year: year, xfxs: id})
          .then(res => {
            this.dqfbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5.信访次数
        // get_http("cjcjcaChunaXfcsBar", {year: year, xfxs: id})
        //   .then(res => {
        //     this.xfcsBarParams.data = res.data.data;
        //   })
        //   .catch(err => {
        //     console.log(err);
        //   });
        this.getBtmAPI(year, id, this.menuName);
      },
      getBtmAPI(year, id, city) {
        // 6.人群年龄
        get_http("cjcjcaChunaRqnlBar", {year: year, xfxs: id, shij_02: city})
          .then(res => {
            this.rqnlBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 7.信访目的
        get_http("cjcjcaChunaXfmdPie", {year: year, xfxs: id, shij_02: city})
          .then(res => {
            this.xfmdPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 8.投诉问题
        get_http("cjcjcaChunaTswtPie", {year: year, xfxs: id, shij_02: city})
          .then(res => {
            this.tswtPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 9.地区信访次数
        // get_http("cjcjcaChunaDqxfcsBar", {year: year, xfxs: id, shij_02: city})
        //   .then(res => {
        //     this.dqxfcsBarParams.data = res.data.data;
        //   })
        //   .catch(err => {
        //     console.log(err);
        //   });
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val, this.curTypeId);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .label
      position absolute
      top -20px
      right 110px
      font-size 18px
      color #2887d7

    .w-popup
      position fixed
      top 0
      left 0
      right 0
      bottom 0
      background rgba(0, 0, 0, 0.4)
      z-index 1005
      font-size 0

      .c-pop
        position absolute
        top 50%
        left 50%
        transform translate(-50%, -50%)
        width 12.7rem

        .search-box
          position absolute
          width 4.3rem
          height 0.5rem
          line-height 0.5rem
          top -0.5rem
          left 0
          // background #293974
          background #0d85be
          padding-left 0.2rem
          border-radius .05rem .05rem 0 0

          input
            width 3.5rem
            box-sizing border-box
            padding 0 0.3rem
            height 0.3rem
            line-height 0.3rem
            color #fff
            font-size 0.16rem
            background transparent
            border 0.01rem solid #fff
            display inline-block
            vertical-align initial

          a
            color #fff
            font-size 0.18rem
            padding 0 0.2rem
            display inline-block
            text-decoration none

        .tableScroll
          overflow hidden
          position relative
          max-height 4.4rem

        .tableWarpper
          overflow hidden
          border-radius 0 0.1rem 0.1rem 0.1rem
          box-shadow 0 0 0.5rem #0e95d2 inset

          .placeholder
            width 100%
            color #fff
            line-height 200px
            text-align center

          .table_tr
            background #0a2341

            &:nth-child(even)
              background #0a1631

          .row
            list-style none
            display flex

            li
              float left
              text-align center
              line-height 0.4rem
              display flex
              justify-content center
              align-items center
              color #fff
              font-size 0.16rem
              border-left 0.01rem solid rgba(255, 255, 255, 0.2)

          .table_title
            height 0.68rem
            line-height 0.68rem
            background #0a1631

            li
              color #2ddfff
              font-size 0.18rem

    .content
      width 100%
      height 100%
      display flex
      flex-direction column
      padding .2rem .3rem
      box-sizing border-box

      .c-top
        flex 1
        display flex

        .ct-left
          flex 2
          height 100%
          position relative
          display flex
          /*border-right 1px dashed #91e3f7*/

          .ct-l-l
            flex 1
            height 100%

            .detail
              height calc(100% - 0.28rem)
              display flex
              flex-direction column
              justify-content center
              align-items center
              .all-count
                height 0.48rem
                padding 0.2rem 0
              .zbPie
                position relative
                flex 1
                width 100%
                display flex
                justify-content center
                align-items center
                .pie-label
                  position relative
                  left 80px
                  width 110px
                  text-align center
                  font-weight bold
                  p
                    color #fff
                    font-weight 600
                    font-size 18px
                    line-height 50px
                    &:nth-child(1)
                      width 110px
                      border-bottom 2px solid rgba(255, 255, 255, 0.7)
                .chart
                  position relative
                  flex 1
                  width 100%
                  height 100%
                  .pie-percent
                    position absolute
                    top 50%
                    left 50%
                    color #e03562
                    transform translate(-50%, -50%)
                    font-size 24px
                    font-weight 700
          .ct-l-r
            flex 1
            height 100%
            position relative

            .charts
              width 100%
              height calc(100% - 0.28rem)

        .ct-right
          flex 1
          height 100%
          position relative
          display flex

          .charts
            width 100%
            height calc(100% - 0.28rem)

          .echartPie
            flex 1
            height 100%

          .echartBar
            flex 1
            height 100%

      .c-bottom
        flex 1
        background url('../../../assets/c-bottom-bg1.png') no-repeat center
        background-size 100% 100%
        position relative
        display flex
        box-sizing border-box
        padding .7rem .3rem 0 .3rem
        margin 0 .2rem .1rem .2rem

        .menu
          position absolute
          top 10px
          left 160px
          width 120px
          height 35px
          z-index 1

          .curitem
            width 120px
            line-height 35px
            font-size 20px
            color #fff
            font-weight 700
            text-align center
            cursor pointer

          ul.menu-list
            position absolute
            top -30px
            left 125px
            width 230px
            padding 15px
            box-sizing border-box
            background-color rgba(9, 39, 109, 0.8)

            li.item
              float left
              width 50%
              font-size 18px
              color #fff
              line-height 50px
              box-sizing border-box
              /*border-bottom 1px solid transparent*/
              padding 0 12px
              cursor pointer
              &:nth-child(odd)
                border-right 1px dashed #91e3f7

              &.active
                text-shadow: 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4
                font-weight 700
        /*border-bottom 1px dashed #91e3f7*/

        .w-charts
          flex 1
          height 100%

          .charts
            width 100%
            height 90%

</style>

