<!--
echarts 圆环进度条
@auth:lyq
-->
<template>
  <div ref="chartEl" :style="{width:params.canvasStyle.width,height:params.canvasStyle.height}"></div>
</template>

<script>
  import echarts from "echarts"

  export default {
    name: "PieProgress",
    props: {
      data: {
        type: Object,
        default() {
          return {
            "value": 0,
            "total": 100
          }
        }
      },
      params: {
        type: Object,
        default: function () {
          return {
            id: 'pie',
            canvasStyle: {
              width: '200px',
              height: '200px'
            },
            options: {
              bgColor: 'rgba(13,69,113,0.5)',
              GradientColor: {
                start: 'rgba(60, 234, 166, 1)',
                end: 'rgba(162, 227, 105, 1)'
              }
            },
            data: {
              value: 50
            }
          }
        }
      },
      animateFlag: Boolean
    },
    data() {
      return {
        chart: null
      }
    },
    mounted() {
      this.initChart();
    },
    methods: {
      initChart() {
        if (this.chart) {
          this.chart.dispose();
          this.chart = null;
        }
        if (this.params.data.length === 0) return;
        this.chart = echarts.init(this.$refs.chartEl);
        let value = (this.params.data[0].value * 100).toFixed(2);
        // let allsum = 0
        // if (this.params.data[0]) {
        //   this.params.data.forEach(element => {
        //     if (element.total) {
        //       allsum = element.total
        //     } else {
        //       allsum += element.value
        //     }
        //   });
        // }
        // let value = 0
        // if (this.params.data[0]) {
        //   value = this.params.data[0].value / allsum * 100
        // }

        this.chart.setOption({
          tooltip: {
            show: false,
            trigger: 'item',
            backgroundColor: this.$STANDARD.tooltipBackgroundColor,
            padding: [10, 20],
            formatter: function () {
              return (this.params.data[0].value * 100).toFixed(2) + '%'
            }
          },
          series: [
            {
              type: 'pie',
              radius: this.params.placeHolderRadius,
              hoverAnimation: false,
              startAngle: 0,
              clockwise: false,
              label: {
                normal: {
                  position: 'center'
                }
              },
              data: [
                {
                  value: value,
                  tooltip: {
                    show: false
                  },
                  itemStyle: {
                    normal: {
                      // color: this.params.options.bgColor
                      color:
                        new echarts.graphic.LinearGradient(
                          0, 0, 1, 1, [{
                            offset: 0,
                            color: this.params.options.bgColor.start || 'rgba(60, 234, 166, 1)'
                          },
                            {
                              offset: 1,
                              color: this.params.options.bgColor.end || 'rgba(162, 227, 105, 1)'
                            }
                          ]
                        )
                    }
                  },
                  label: {
                    normal: {
                      show: true,
                      textStyle: {
                        fontSize: 20,
                        color: '#fff'
                      }
                    },

                  }
                }
              ],
            },
            {
              type: 'pie',
              radius: this.params.realRadius,
              hoverAnimation: false,
              startAngle: 0,
              clockwise: false,
              label: {
                normal: {
                  position: 'center'
                }
              },
              data: [
                {
                  value: value,
                  itemStyle: {
                    normal: {
                      // shadowColor: this.params.options.GradientColor.end || 'rgba(162, 227, 105, 1)',
                      // shadowBlur: 10,
                      color:
                        new echarts.graphic.LinearGradient(
                          0, 0, 1, 1, [{
                            offset: 0,
                            color: this.params.options.GradientColor.start || 'rgba(60, 234, 166, 1)'
                          },
                            {
                              offset: 1,
                              color: this.params.options.GradientColor.end || 'rgba(162, 227, 105, 1)'
                            }
                          ]
                        )
                    }
                  }
                },
                {
                  value: 100 - value,
                  tooltip: {
                    show: false
                  },
                  radius: ['55', '60'],
                  itemStyle: {
                    normal: {
                      color: 'transparent'
                    }
                  },
                  label: {
                    normal: {
                      show: false,
                      // formatter: value + '%',
                      textStyle: {
                        fontSize: 20,
                        color: '#fff'
                      }
                    },
                  }
                }
              ]

            },
          ]
        })
      }
    },
    watch: {
      animateFlag: {
        handler(newVal, oldVal) {
          this.initChart();
        },
        deep: true
      },
      'params.data': {
        handler(newVal, oldVal) {
          this.initChart();
        },
        deep: true
      },
    }
  }
</script>

